import * as React from "react";
import Markdown from "react-remarkable";

const StepMessage = ({ actions, message, handleAction, isLoading }) => (
  <div className="StepMessage-wrapper">
    <div className={`markdown-wrapper ${message.level || ""}`}>
      <Markdown options={{html: message.trusted_html}}>{message.contents}</Markdown>
    </div>
    <div className="u-marginTop--30 flex justifyContent--flexEnd">
      {!actions ? null : actions.map(
        (action, index) => {
          return (
            <button
              key={index}
              className={`btn ${action.buttonType}`}
              onClick={() => handleAction(action)}
              disabled={isLoading}>{isLoading ? action.loadingText : action.text}
            </button>
          );
        }
      )}
    </div>
  </div>
);

export default StepMessage;
